<script>
import Steps from './steps/Steps.vue'
import VueRecaptcha from 'vue-recaptcha';

var verify = false;

export default {
	name: 'Body',
	components: {
		Steps,
		VueRecaptcha
	},

	data(){
		return {
			showErrorCapcha : false,
			currentStep: 1,

			form : {
				dni: '',
				nombre: '',
				telefono: '',
				email: '',
				interes: '',
				mensaje: ''
			}
		}
	},

	methods: {

		enviar()
		{
			this.$http.post('api.php', JSON.stringify(this.form)).then(function(r){
			}),function(r){}
		},

		onVerify(){
			verify = true,
			this.showErrorCapcha = false;
		},

		goToStep() {
			
			this.currentStep = this.currentStep+1;
			if(this.currentStep==2)
				this.enviar();
		},

	    onSubmit() {
	    },

		validateForm(scope) {
			this.$validator.validateAll(scope).then((result) => {
				if (result){

					switch(this.currentStep){

						case 1:
							this.goToStep();
							/*
							if(btoa(this.form.cupon) == 'ajQ5bzh0'){
								this.goToStep();
							}else{
								try{
									this.form.cupon = '';
									document.querySelector('.cupon').value = '';
								}catch(err){}
							}
							*/

						break;


						case 2:
							if(verify){
								this.goToStep();
							}else{
				              this.showErrorCapcha = true;
							}
						break;

						default:
							this.goToStep();
					}

				}
			});
		}

	}

}
</script>