import Vue from 'vue'
import App from './App.vue'
import VeeValidate, { Validator } from 'vee-validate';
import es from './requires/es';
import VueResource from 'vue-resource';
import axios from 'axios';

import 'normalize.css'
import 'animate.css'


const isUnique = (value) => {
  return axios.post('verify.php', { cupon: value }).then((response) => {
    if(response.data.status)
    {
		return {
			valid: true,
		};
    }else{
		return {
			valid: false,
			data: {
				message: `'${value}' cupon invalido.`
			}
		};
    }
  });
};


Vue.use(VeeValidate);

Validator.extend('unique', {
  validate: isUnique,
  getMessage: (field, params, data) => {
    return data.message;
  }
});

Validator.localize('es', es);
Validator.localize('es', {
  custom: {
    terms: {
      required: 'Debes aceptar la autorización.'
    }
  }
});


Vue.use(VueResource);
new Vue({
  render: h => h(App),
}).$mount('#app')